<template>
    <div class="login">
        <img class="banner" :src="appConfig.login.banner" alt>
        <div class="form">
            <div class="input-group phone">
                <img :src="appConfig.login.phone" alt>
                <input type="text" v-model="form.phone" placeholder="输入手机号码">
            </div>
            <div class="input-group phone">
                <img :src="appConfig.login.password" alt>
                <input type="password" v-model="form.password" placeholder="输入密码">
            </div>
            <cube-button
                class="code-btn"
                :style="[appConfig.mainBackground]"
                @click="login"
                :primary="true"
            >登录</cube-button>
            <!--<p-->
                <!--class="danger text-center"-->
                <!--@click="$store.commit('setMsg', '暂未开放注册通道')"-->
                <!--:style="[appConfig.mainColor]"-->
            <!--&gt;没有账号？立即注册</p>-->
            <cube-checkbox class="agreement" shape="square" v-model="check">我同意</cube-checkbox>
            <span class="xy" @click="$router.push('/agreement')">《用户注册协议》</span>
            <span class="xy" @click="$router.push('/policy')">《隐私协议》</span>

        </div>
    </div>
</template>

<script>
import { mapState } from 'vuex'
import { checkPhone } from '../utils/validator.js'
import { brigeNativeByJavascript } from '../api/Bridge.js'
export default {
    data() {
        return {
            form: {
                phone: undefined,
                password: ''
            },
            check: false
        }
    },
    methods: {
        login() {
            if (!checkPhone(this.form.phone, true)) return
            if (this.form.password.length < 6) {
                this.$store.commit('setMsg', '密码长度不能小于6位')
                return
            }
            if (this.form.phone === '18068798306' && this.form.password === 'ceshi123') {
                if (this.check == false){
                    this.$store.commit('setMsg', '请阅读并同意协议')
                    return
                }
                this.$store.commit('setUser', { phone: this.form.phone, isLogin: true })
                this.$store.commit('setMsg', '登录成功')
                setTimeout(() => {
                    brigeNativeByJavascript('closeRefundApplication')
                }, 1000)
            } else {
                this.$store.commit('setMsg', '账号或密码错误')
            }
        }
    },
    computed: {
        ...mapState(['appConfig', 'user'])
    },
    beforeCreate() {
        document.title = '登录'
    }
}
</script>

